.logo {
  display: flex;

  padding-left: 10px;
}

.logo__wrapper {
  margin-left: 16px;
  padding-top: 17px;
}

.logo__title {
  margin: 0;

  color: $contrast-text-color;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  line-height: 29px;
}

.logo__subtitle {
  margin: 0;

  color: $logo-subtitle-color;

  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  line-height: 14px;
}
