@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  src: local("Roboto Condensed"), local("RobotoCondensed-Regular"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/roboto-500.woff2") format("woff2"), url("../fonts/roboto-500.woff.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  src: local("PT Mono"), local("PTMono-Regular"), url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff"); }

html {
  box-sizing: border-box; }

body {
  color: #666666;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  line-height: 22px; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: #666666; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0; }

.header {
  min-height: 881px;
  background-color: #1b2d37;
  background-image: url("../img/background-desktop@1x.png"); }

.header__wrapper {
  width: 1420px;
  margin: 0 auto; }

.header__logo-wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 22px; }

.header__list {
  display: flex;
  justify-content: space-between;
  padding: 0;
  list-style-type: none; }

.header__item a {
  color: #ffffff; }

.header__item--mail,
.header__item--tel {
  padding-top: 10px;
  padding-left: 50px; }

.header__item--tel {
  margin-right: 50px; }

.header__item--mail {
  margin-right: 73px; }

.logo {
  display: flex;
  padding-left: 10px; }

.logo__wrapper {
  margin-left: 16px;
  padding-top: 17px; }

.logo__title {
  margin: 0;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  font-weight: bold;
  font-style: normal;
  line-height: 29px; }

.logo__subtitle {
  margin: 0;
  color: #788891;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  font-style: normal;
  line-height: 14px; }

.button {
  height: 45px;
  padding: 0 25px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background: none;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: bold;
  font-style: normal;
  line-height: 16px; }
  .button--header {
    color: #ffffff; }
  .button--main-screen {
    height: 55px;
    margin: 44px 0 0 98px;
    padding: 0 45px;
    text-align: center;
    color: #ffffff;
    border-color: #00B2FF;
    background-color: #00B2FF;
    font-size: 16px;
    line-height: 19px; }

.main-screen {
  position: relative;
  min-height: 500px;
  margin-top: 217px; }

.main-screen img {
  position: absolute;
  top: -57px;
  right: -81px; }

.main-screen__description {
  position: relative;
  padding-left: 6px;
  color: #ffffff; }

.main-screen__description h2 {
  margin: 0;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: bold;
  font-style: normal;
  line-height: 94px; }

.main-screen__description p {
  position: relative;
  width: 650px;
  margin: 26px 0 0 98px;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 22px;
  font-weight: normal;
  font-style: normal;
  line-height: 34px; }
  .main-screen__description p::before {
    position: absolute;
    top: 16px;
    left: -94px;
    width: 66px;
    height: 2px;
    content: "";
    background-color: #ffffff; }

.main-screen__scroll {
  position: absolute;
  top: 474px;
  left: 10px;
  transform: rotate(-90deg);
  transform-origin: left top 0;
  opacity: 0.5;
  color: #ffffff;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  line-height: 16px; }
